


#menu-below-board {
    display: 'inline-flex'; 
    flex-direction: 'row';
    justify-content: 'space-between';
 }



#body-grid {
    display: grid;
    grid-template: auto 1fr auto / auto 1fr auto;
  }

#title {
    grid-column: 1 / span 12;
    padding-left: 5rem;
  }

#nav {
    grid-column: 1 / 2;
    padding-left: 5rem;
  }

#mid {
    margin-top: 12rem;
    grid-column: 5 / 11;
    display: flex
  }

  body {
      display: inline
  }


  nav {
    display: inline;
}

h2 {
  margin-top: 0;
}




.shake-board {
  /* animation: endgame infinite 5s */
  max-width: 28rem;
  max-height: 28rem;

}

@keyframes endgame {
  0% {
    padding-left: 0rem
  }

  50% {
    padding-left: 10rem
  }

  100% {
    padding-left: 0rem
  }
  
}